import Vue from 'vue';
import Vue3 from '@vue/composition-api';

// import languagePlugin from './utils/lang.plugin';

import languagePlugin from './plugins/lang';
import requestPlugin from './plugins/req';

import store from './store';
import router from './router';

import App from './App.vue';

// Global layout components
import CPage from './components/App/CPage.vue';

Vue.config.productionTip = false;

Vue.use(Vue3);
Vue.use(languagePlugin);
Vue.use(requestPlugin);

Vue.component('CPage', CPage);

new Vue({
	store,
	router,
  render: h => h(App),
}).$mount('#app');
