// import request from './../utils/request'
import store from './../store/'

export default {
	install(Vue) {
		Vue.prototype.$lang = (key) => {
			let langObject = store.getters.langObject;
			const lang = store.getters.lang;

			if(!langObject) {
				store.dispatch('fetchLang', lang);
			} else {
				// if(langObject)
				return langObject[key];
			}
		}
	}
}