import Vue from 'vue'
import Store from 'vuex'

import req from './../utils/request';

// import Request from './../utils/req.plugin.js'

// console.log(Request)

Vue.use(Store)
// Vue.use(Request)

const store = new Store.Store({
  state: {
    msg: false,
    auth: false,
    cart: {},
    stores: {},
    lang: 'ru',
    langObject: null,
    currentStore: null
  },
  mutations: {
    setStores(state, stores) {
      state.stores = stores;
    },
    setCurrentStore(state, storeId) {
      state.currentStore = storeId;
    },
    setCartFromStorage(state) {
      const storageCart = localStorage.getItem('cart');

      if(storageCart) {
        state.cart = JSON.parse(storageCart);
      }
    },
    cartUpdate(state, store) {
      let totalPrice = 0;
      let amountGoods = 0;
      const storeId = Object.keys(store)[0];

      for(const goodId in store[storeId].goods) {
        const good = store[storeId].goods[goodId];

        totalPrice += good.added * good.price;
        amountGoods += store[storeId].goods[goodId].added;
      }

      store[storeId].totalPrice = totalPrice;
      store[storeId].amount = amountGoods;

      let cart = {...state.cart, ...store};

      if(Object.keys(store[storeId].goods).length <= 0) {
        delete cart[storeId];
      }

      localStorage.setItem('cart', JSON.stringify(cart))
      state.cart = cart;
    },
    msg(state, msg) {
      state.msg = msg;
    },
    clearMsg(state) {
      state.msg = false;
    },
    setAuth(state) {
      localStorage.setItem('user', true);
      state.auth = true;
    },
    clearAuth(state) {
      localStorage.removeItem('user');
      state.auth = false;
    },
    setLangObject(state, langObject) {
      state.langObject = langObject;
    }
  },
  actions: {
    async fetchLang({commit}, lang) {
      req('lang?lang=' + lang, 'GET')
        .then(res => {
          store.commit('setLangObject', res[lang]);
        })
    },
    // async switchLang({dispatch, commit}, lang) {
    //   await 
    //   dispatch('fetchLang')

    //   state.lang = lang;
    //   // state.langObject = null;
    // },
    async fetchStores({commit}, pageNum) {
      await req('stores?page=' + pageNum, 'GET')
        .then(res => {
          // console.log(res)
          commit('setStores', res.stores)
        })
        .catch(err => {
          console.error('Fetch shops error', err)
        })
    },
    async setUser({ commit }) {
      if(localStorage.getItem('user')) {
        commit('setAuth')
      } else {
        try {
          await req('auth?check', 'GET')
            .then(res => {
              if(res.status === 200) {
                commit('setAuth')
              }
            })
            .catch(err => {
              console.error('Error auth', err)
            })
          
        } catch(error) {
          commit('setError', error)
        }
      }
      
    }
    // async fetchCurrency() {
    //   // const key = process.env.VUE_APP_FIXER
    //   // const res = await fetch(`http://data`)
    //   // return await res.json()
    // }
  },
  getters: {
    msg: state => state.msg,
    auth: state => state.auth,
    cart: state => state.cart,
    stores: state => state.stores,
    currentStore: state => state.currentStore,
    lang: state => state.lang,
    langObject: state => state.langObject
  },
  modules: {
  }
})

export default store
