import Vue from 'vue';
import Router from 'vue-router';

import request from './../utils/request';

import Store from './../store/';

import messages from './../assets/messages/ru.js';

Vue.use(Router);

// const path = './../pages/Home-v2.vue'
const routes = [
	{
		// path: '/:store?/:good?',
		path: '/',
		name: 'Home',
		meta: { headerMod: 'header_biggest'},
		component: () => import('./../pages/Homev2')
	},
	{
		path: '/page',
		name: 'Page',
		meta: {},
		redirect: '/page/about',
		component: () => import('./../pages/Page/'),
		children: [
			{
				path: 'about',
				component: () => import('./../pages/Page/About')
			},
			{
				path: 'business',
				component: () => import('./../pages/Page/Business')
			}
		]
	},
	{
		path: '/cart',
		name: 'Cart',
		component: () => import('./../pages/Cart')
	},
	// {
	// 	path: '/business',
	// 	name: 'Business',
	// 	component: () => import('./../pages/Business')
	// },
	{
		path: '/store/new',
		name: 'StoreNew',
		component: () => import('./../pages/StoreNew')
	},
	{
		// path: '/:store?/:good?',
		path: '/stores/:country?/:city?',
		name: 'Stores',
		meta: { headerMod: 'header_biggest'},
		component: () => import('./../pages/Stores')
	},
	{
		path: '/auth',
		name: 'Auth',
		meta: {},
		redirect: '/auth/signin',
		component: () => import('./../pages/Auth/'),
		children: [
			{
				path: 'signin',
				component: () => import('./../pages/Auth/Signin')
			},
			{
				path: 'signup',
				component: () => import('./../pages/Auth/Signup')
			},
			{
				path: 'simply',
				component: () => import('./../pages/Auth/Simply')
			}
		]
	},
	{
		path: '/board',
		name: 'Board',
		meta: { auth: true, header: 'user'},
		component: () => import('./../pages/Board')
	},
	{
		path: '/explore',
		name: 'Explore',
		component: () => import('./../pages/Explore/')
	},
	{
		path: '/new',
		name: 'New',
		meta: {auth: true},
		component: () => import('./../pages/New')
	},
	{
		path: '/calendar',
		name: 'Calendar',
		component: () => import('./../pages/Calendar')
	},
	{
		path: '*',
		name: 'Store',
		component: () => import('./../pages/Store.vue')
	}
];

const router = new Router({
	mode: 'history',
	base: '/',
	routes,
	scrollBehavior(to, from, savedPosition) {
	  if(savedPosition) {
	    return savedPosition;
	  } else {
	    return { x: 0, y: 0 };
	  }
	}
});



router.beforeEach(async (to, from, next) => {

	// console.log('QUREry')
	if(to.query.msg) {
		const msg = to.query.msg;

    if(msg in messages) {
      Store.commit('clearMsg')
      // setTimeout(() => {
	      Store.commit('msg', messages[msg])
      // }, 100)
      // return messages[msg]
    }
		// console.log("srote!#$#@")	
		// console.log(Store)	
	}
	// if local storage or cookie or store then
	// console.log(store.getters.lang)
	// store

	// или мы проверяем стейт и записываем в переменную если стейт есть то мы не делаем запрос, 
	// если же стейта не ну то мы делаем запрос и результат записываем в стейт

				// router.auth = true
				// console.log(to.path)
	if(to.path.includes('/auth')) {
		try {
			await request('auth?check', 'HEAD')
				.then(() => {
					if(from.path == '/board') {
						next('/')
					}
					
					next('/board');
				})
				.catch(() => {
					next();
				});
		} catch(e) {
			console.error(e);
			next();
		}
	}
	
	if(to.meta.auth) {
		const goto = '/auth?msg=signin-first';

		try {
			await request('auth?check', 'HEAD')
				.then(() => {
					next();
				})
				.catch(() => {
					next(goto);
				});
		} catch(e) {
			console.error(e);
			next(goto);
		}
	}

	next();
});

export default router